.Checkbox{display:flex;align-items:flex-end;margin-bottom:5px}.Checkbox__label{display:inline-flex;align-items:center;font-size:1rem;color:#374151;cursor:not-allowed}.Checkbox__input{margin-right:.5rem;margin-bottom:0px;width:1.2rem;height:1.2rem;appearance:none;border:1px solid #d1d5db;border-radius:.375rem;background-color:#fff;position:relative;transition:background-color .2s ease-in-out,border-color .2s ease-in-out}.Checkbox__input:checked{background-color:#5c6ac4;border-color:#5c6ac4}.Checkbox__input:checked::after{content:"✓";position:absolute;top:50%;left:50%;transform:translate(-50%, -55%);font-size:.75rem;color:#fff}.Checkbox__input:disabled{cursor:not-allowed}.Checkbox__input:disabled:not(:checked){background-color:#f9fafb}.Checkbox__input:disabled:checked{background-color:#5c6ac4;border-color:#5c6ac4}